* {
  margin: 0;
  padding: 0;
  font-family: sans-serif;
}

h1 {
  position: absolute;
  color: gray;
  opacity: 0.7;
  left: 50%;
  top: 5%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.wrapper {
  min-height: 100vh;
  width: 100%;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background: #222222;
}

input {
  height: 30px;
  width: 250px;
  border: none;
  border-bottom: 1px solid whitesmoke;
  background: none;
  color: white;
  padding: 0 10px;
  outline: none;
}

.enter {
  width: 100px;
  height: 30px;
  font-size: 12px;
  background: none;
  border: 1px solid whitesmoke;
  color: whitesmoke;
  margin-left: 10px;
}

.enter:hover {
  background-color: whitesmoke;
  color: #222222;
  cursor: pointer;
}

.todo_item {
  position: absolute;
  cursor: move;
  min-width: 215px;
  padding: 1em;
  border-radius: 5px;
}

.delite {
  position: absolute;
  right: 5px;
  top: 5px;
  background: none;
  border: none;
}

.delite:hover {
  color: red;
  cursor: pointer;
}
